<template>
  <div style="padding-left: 20px; padding-right: 20px; padding-top: 10px">
    <el-table :data="dataList" height="500" v-loading="loading" element-loading-text="......" style="width: 100%">
      <el-table-column prop="title" label="" width="100"></el-table-column>
      <el-table-column prop="tip" label=""></el-table-column>
      <el-table-column prop="icon" label=""></el-table-column>
      <!--<el-table-column prop="publish_id" label=""></el-table-column>-->
      <!--<el-table-column prop="create_time" label=""></el-table-column>-->
      <el-table-column prop="route_url" label=""></el-table-column>
      <!--<el-table-column prop="status" label=""></el-table-column>-->
      <!--<el-table-column prop="seq_num" label=""></el-table-column>-->
      <el-table-column label="" width="150">
        <template slot-scope="props">
          <div>
            <el-button type="text" @click="doEdit(props.row)"></el-button>
            <el-button type="text" @click="doDelete(props.row)"></el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>
    <el-row style="margin-top: 20px">
      <el-col :span="14" :offset="5">
        <el-form :model="form" ref="form" :rules="rules" label-width="100px">
          <el-row>
            <el-col :span="12">
              <el-form-item label="" prop="title">
                <el-input placeholder="" size="small" v-model="form.title"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="" prop="tip">
                <el-input placeholder="" size="small" v-model="form.tip"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-form-item label="" prop="icon">
            <el-input placeholder="" size="small" v-model="form.icon"></el-input>
          </el-form-item>
          <el-row>
            <el-col :span="12">
              <el-form-item label="" prop="route_url">
                <el-input placeholder="" size="small" v-model="form.route_url"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="" prop="status">
                <el-switch
                  v-model="form.status"
                  active-color="#13ce66"
                  inactive-color="#ff4949"
                  :active-value="1"
                  :inactive-value="0"
                >
                </el-switch>
                <el-button style="margin-left: 30px" @click="save()" size="small"></el-button>
                <el-button type="primary" style="margin-left: 30px" @click="save()" size="small"></el-button>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </el-col>
    </el-row>
  </div>
</template>
<script>
  import {queryList, saveOrUpdate, del} from '../../service/AppIconsService';
  export default{
    data: function () {
      return {
        title: '',
        form: this.initForm(),
        dialogMode: "save",
        show: false,
        dataList: [],
        rules: {
          title: [
            {required: true, message: '', trigger: 'blur'},
          ],
          tip: [
            {required: true, message: '', trigger: 'blur'},
          ],
          icon: [
            {required: true, message: '', trigger: 'blur'},
          ],
          route_url: [
            {required: true, message: '', trigger: 'blur'},
          ],
          status: [
            {required: true, message: '', trigger: 'blur'},
          ],
        }
      }
    },
    computed: {},
    created: function () {
      this.refresh();
    },
    methods: {
      refresh() {
        const that = this;
        that.loading = true;
        queryList({}).then(res => {
          that.loading = false;
          that.dataList = res.data;
        }).catch(res => {
          that.$message.error("" + res);
          that.loading = false;
        });
      },
      save() {//
        const that = this;
        this.$refs['form'].validate((valid) => {
          if (!valid) {
            return;
          }
          const data = {...that.form, type: 1};
          saveOrUpdate(that.dialogMode, data).then(res => {
            that.show = false;
            that.$message.success("!");
            that.refresh();
            window.parent.store.commit("app/initDesktopApp");
            that.addDialog();
          }).catch(res => {
            that.$message.error("!" + res);
          });
        });
      },
      initForm() {//
        return {
          type: null,
          title: null,
          tip: null,
          icon: null,
          publish_id: null,
          create_time: null,
          parent_id: null,
          route_url: null,
          status: 1,
          name: ''
        }
      },
      addDialog() {//
        this.dialogMode = "save";
        this.form = this.initForm();
      },
      doEdit(row) {//
        this.dialogMode = "update";
        this.form = {...row};
      },
      doDelete(row) {
        const that = this;
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          del([row.id]).then(res => {
            this.$message.success("");
            window.parent.store.commit("app/initDesktopApp");
            that.refresh();
          }).catch(res => {
            that.$message.error("" + res);
          });
        }).catch(() => {
        });
      }
    },
    components: {}
  }
</script>
<style>

</style>



